import "./App.css";
import { useState } from "react";
import Sidebar from "./components/Sidebar";
import Content from "./components/Content";
import pic from "./Media/Warframe0040.jpg";
import { Spring } from "react-spring/renderprops";
import { CSSTransition } from "react-transition-group";
import { animated } from "react-spring/renderprops-universal";
import styled from "styled-components";

function App() {
  const [page, setpage] = useState("Home");
  const [visible, setvisible] = useState(true);

  let sidebarClassname = visible ? "sidebar" : "sidebar-open";
  let contentClassname = visible ? "content" : "content-open";
  return (
    <>
      <img
        src={pic}
        style={{ width: 25, height: 25, cursor: "pointer", position: "fixed" }}
        onClick={() => setvisible(!visible)}
      />
      <Sidebar className={sidebarClassname} setpage={setpage} />
      <Content className={contentClassname} page={page} />
    </>
  );
}

export default App;
