import { useState } from "react";
import pic from "../Media/Warframe0040.jpg";
import "../css/Sidebar.css";
const Sidebar = ({ setpage, className }) => {
  const [visible, setvisible] = useState(true);
  return (
    <div className={className}>
      <div className="click"></div>
      <div className="click" onClick={() => setpage("Home")}>
        Home
      </div>
      <div className="click" onClick={() => setpage("Projects")}>
        Projects
      </div>
      <div className="click" onClick={() => setpage("About")}>
        About
      </div>
      <div className="click" onClick={() => setpage("Resume")}>
        Resume
      </div>
    </div>
  );
};

export default Sidebar;
